import { Fragment } from "react"

import classes from "./technologies.module.css"
import { apiRootURL } from "../apiConfigs"

const Technologies = (props) => {
    let content = props.technologies.length>0 && props.technologies.map((tech)=>{
            return (
                <div key={tech._id} className={classes["technology-icon"]}>
                    <img src={apiRootURL+tech.iconUrl} alt={tech.name} />
                    <h6 className={classes["tech-name"]}>{tech.name}</h6>
                </div>
            )
        })

    if(props.error){
        content = <div>
            Something went wrong.
        </div>
    }

    return (
        <Fragment>
            {props.title && <p className="title">{props.title}</p>}
            <div className={props.error? classes["error"]:classes["technology-list"]}>
                {content}
            </div>
        </Fragment>
    )
}

export default Technologies