import { useState, useEffect, useContext, Fragment } from "react";
import { Link, useParams, useHistory } from "react-router-dom";

import classes from "./project.module.css";
import HiThere from "../components/hiThere";
import Spinner from "../components/UI/spinner";
import Modal from "../components/UI/modal";
import Technologies from "../components/technologies";
import Fancybox from "../components/UI/fancyBox";
import { apiRootURL, tokenIndentation } from "../apiConfigs";
import AuthContext from "../store/authContexts";
import TrashIcon from "../assets/icons/garbage.png"
import EditIcon from "../assets/icons/edit.png"

const Project = () => {
  const { projectId } = useParams();
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  const [project, setProject] = useState(null);
  const [error, setError] = useState("");
  const [errorHappened, setErrorHappened] = useState(false);
  const [deleteProjectAsk, setDeleteProjectAsk] = useState(false);
  const [responseMessage, setresponseMessage] = useState(null);
  const [adminMenuOpened, setAdminMenuOpened] = useState(false);

  const authCTX = useContext(AuthContext)

  let errorExists = false



  useEffect(() => {
    fetch(apiRootURL + "development/projects/" + projectId)
      .then((res) => {
        if (!res.ok) {
          errorExists = true;
        }
        return res.json();
      })
      .then((jsonRes) => {
        if (errorExists) {
          const error = new Error("Error: " + jsonRes.message);
          throw error;
        }
        const fetchedProject = jsonRes;
        return setProject(fetchedProject);
      })
      .then(() => {
        setLoading(false);
      })
      .catch((err) => {
        setErrorHappened(true)
        setError(err.message);
        setLoading(false);
      });
  }, [projectId]);

  const closeModal = () => {
    setError(false);
    setDeleteProjectAsk(false);
    setresponseMessage(null);
  };

  const navigateToHome = () => {
    setError(false);
    setDeleteProjectAsk(false);
    setresponseMessage(null);
    history.push("/");
  };

  const openDeleteModal = () => {
    setDeleteProjectAsk(true);
  };

  const deleteProjectHandler = () => {
    setLoading(true);
    const URL = apiRootURL + "development/projects/" + projectId;
    const METHOD = "DELETE";
    fetch(URL, {
      method: METHOD,
      headers : { Authorization : tokenIndentation+authCTX.token}
    })
      .then((res) => {
        if (!res.ok) {
          errorExists = true;
        }
        return res.json();
      })
      .then((jsonRes) => {
        if (errorExists) {
          const error = new Error("Error: " + jsonRes.message);
          throw error;
        }
        setDeleteProjectAsk(false);
        setLoading(false);
        setresponseMessage(jsonRes.message);
      })
      .catch((err) => {
        setErrorHappened(true)
        setDeleteProjectAsk(false);
        setError(err.message);
        setLoading(false);
      });
  };

  const openWebsite = () => {
    window.open(project.link, "_blank");
  };

  const toggleAdminMenu = () => {
    setAdminMenuOpened(prevState=>{
      return !prevState
    })
  }

  return (
    <Fragment>
      {loading && <Spinner />}
      {error && (
        <Modal error={true} onClose={closeModal}>
          {error}
        </Modal>
      )}
      {responseMessage && (
        <Modal error={false} onClose={navigateToHome}>
          {responseMessage}
        </Modal>
      )}
      {errorHappened && (
        <div className={classes["error-container"]}>
          <div className={classes["error-message"]}>Something went wrong.</div>
          <Link className="link-item mt-3" to={"/projects"}>
            Go back to All Projects
          </Link>
        </div>
      )}
      {!error && !loading && !errorHappened && (
        <div className={classes["project-page"]}>
          <div className={classes["container"]}>
            <div className={classes.title}>{project.title}</div>
            <div className={classes.details}>
              <div className={classes.description}>
                <div className={classes["desc-text"]}>
                  This project was made from{" "}
                  {project.from + " to " + project.to}.
                </div>
                {project.link && (
                  <div className={classes["link-text"]}>
                    <span className="link-item" onClick={openWebsite}>
                      Go to Project
                    </span>
                  </div>
                )}
              </div>
              <div className={classes["project-details"]}>
                <div className={classes.detail}>
                  <span className="bold">Client: </span>
                  {project.client}.
                </div>
                <div className={classes.detail}>
                  <span className="bold">Services: </span>
                  {project.services.map((service, i, arr) => {
                    if (i + 1 !== arr.length) {
                      return service + ", ";
                    } else {
                      return service + ".";
                    }
                  })}
                </div>
                <div className={classes.detail}>
                  <span className="bold">Category: </span>
                  {project.category}.
                </div>
                <div className={classes["icons"]}></div>
              </div>
            </div>
          </div>
          <div className={classes["main-img-container"]}>
            <img 
              src={apiRootURL + project.mainImageUrl}
              alt={project.title}
              data-fancybox="gallery"
              data-src={apiRootURL + project.mainImageUrl}
            />
            {project.projectImagesUrls && (
              <div className={classes["fancy-photos-container"]}>
                <Fancybox options={{ infinite: false }}>
                  {project.projectImagesUrls.map((img, i) => {
                    return (
                      <div style={{ backgroundImage: "url("+ apiRootURL + img +")" }}  data-fancybox="gallery"  data-src={apiRootURL + img} key={i} className={classes["fancy-photo"]}>
                      </div>
                    );
                  })}
                </Fancybox>
              </div>
            )}
          </div>
          <div className={classes["desc"]}>
            <HiThere title={"Description"} description={project.description} />
            <div className={classes["tech-container"]}>
              <Technologies technologies={project.technologies} />
            </div>
          </div>

          {/* admin only */}
          {authCTX.isLoggedIn && (
            <div onClick={toggleAdminMenu} className={(adminMenuOpened? classes.show:"") +  " " + classes["admin-controls"] + " neum-element"}>
            <div className={classes["controls-container"] + " neum-element"}>
            <Link
              className={classes["admin-control"] + " hover-neum-element"}
              to={"/dashboard/edit-project/" + projectId}
            >
              <span className={classes["text"]}>Edit Project</span>
              <span className={classes["icon"]}>
                <img src={EditIcon} alt="icon" />
              </span>
            </Link>
            <button
              className={classes["admin-control"] + " hover-neum-element"}
              onClick={openDeleteModal}
            >
              <span className={classes["text"]}>Delete Project</span>
              <span className={classes["icon"]}>
                <img src={TrashIcon} alt="icon" />
              </span>
            </button>
            </div>
          </div>
          )}
          {/* admin only */}
        </div>
      )}
      {deleteProjectAsk && (
        <Modal onClose={closeModal} onConfirm={deleteProjectHandler}>
          Are you sure you want delete Project?
        </Modal>
      )}
    </Fragment>
  );
};

export default Project;
